body {
    background-color: #121212;
    color: #ffffff;
    font-family: Arial, sans-serif;
}

button {
    background-color: #3D3D3D;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    margin: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #5E5E5E;
}

input {
    background-color: #3D3D3D;
    color: #ffffff;
    border: none;
    padding: 10px;
    margin: 10px;
}

select {
    background-color: #3D3D3D;
    color: #ffffff;
    border: none;
    padding: 10px;
    margin: 10px;
    appearance: none;
}

.timer-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    transition: background-color 0.5s; /* Добавлена анимация */
}

.blink {
    background-color: #5E5E5E; /* Изменение цвета при мигании */
}

.timer-display {
    font-size: 2em;
    margin-bottom: 20px;
}
